<template>
  <v-container
    fluid
    fill-height
    :style="{
      backgroundPosition: 'center center',
      backgroundSize: 'cover',
      backgroundColor: '#ffffff',
      backgroundImage:
        'linear-gradient(to bottom, rgba(255, 255, 255, 0.85) 100%, rgba(255, 255, 255, 0.85) 100%), url(img/background2.jpg)',
    }"
  >
    <v-layout align-center justify-center>
      <v-flex sm8>
        <v-card class="elevation-12">
          <v-toolbar dark flat>
            <v-toolbar-title>{{ $t("login.header") }}</v-toolbar-title>
            <v-spacer></v-spacer>
          </v-toolbar>
          <!-- uninitialized server: show setup form -->
          <v-card-text v-if="selectedServer && !selectedServer.initialized">
            <br />
            <b
              >Setup Music Assistant server
              {{ selectedServer.friendly_name }}</b
            >
            <br />
            <span
              >In order to use the MusicAssistant server, you must setup a
              username and password to protect the server. When you click
              submit, the server will be setup and you can login with the
              created credentials.</span
            >
            <br /><br />
            <v-form ref="form" v-model="valid" method="post">
              <v-text-field
                v-model="username"
                prepend-icon="mdi-account"
                label="Username"
                type="text"
                required
                autocomplete="new-password"
                :rules="[(v) => !!v || 'Username is required']"
                @keyup.enter="submit"
              ></v-text-field>
              <v-text-field
                v-model="password"
                prepend-icon="mdi-lock"
                label="Password"
                type="password"
                required
                autocomplete="new-password"
                :rules="[
                  (v) =>
                    v.length >= 8 ||
                    'Password must have minimum of 8 characters',
                ]"
                @keyup.enter="submit"
              ></v-text-field>
              <v-text-field
                id="password"
                v-model="password2"
                prepend-icon="mdi-lock"
                label="Repeat password"
                type="password"
                required
                :rules="[password === password2 || 'Passwords do not match']"
                @keyup.enter="submit"
              ></v-text-field>
              <br />
            </v-form>
            <v-btn
              width="100%"
              :disabled="!valid"
              color="success"
              class="mr-4"
              @click="submitSetup()"
              @mouseover="$refs.form.validate()"
              >{{ $t("login.submit") }}</v-btn
            >
          </v-card-text>
          <!-- regular login form -->
          <v-card-text v-else>
            <v-form ref="form" v-model="valid" lazy-validation>
              <br />
              <v-select
                v-model="selectedServer"
                :items="servers"
                :label="$t('login.server')"
                :item-text="
                  function (item) {
                    return `${item.friendly_name} (${item.ip_address})`;
                  }
                "
                return-object
                prepend-icon="mdi-server-network"
              ></v-select>
              <v-text-field
                v-model="username"
                :label="$t('login.username')"
                prepend-icon="mdi-account"
                type="text"
                :rules="validateUsername"
                :disabled="!selectedServer"
                name="username"
              ></v-text-field>
              <v-text-field
                v-model="password"
                :label="$t('login.password')"
                prepend-icon="mdi-lock"
                type="password"
                :rules="validatePassword"
                :disabled="!selectedServer"
                name="password"
                @keyup.enter="submitLogin()"
              ></v-text-field>
              <v-checkbox
                v-model="permanentSession"
                :label="$t('login.save_creds')"
                :disabled="!selectedServer"
              ></v-checkbox>
            </v-form>
            <v-btn
              width="100%"
              :disabled="!valid"
              color="success"
              class="mr-4"
              @click="submitLogin()"
              @mouseover="$refs.form.validate()"
              >{{ $t("login.login") }}</v-btn
            >
          </v-card-text>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
  import axios from "axios";
  import Vue from "vue";

  export default Vue.extend({
    name: "LoginView",
    props: {},
    data() {
      return {
        servers: [],
        selectedServer: null,
        selectedServerAddress: "",
        username: "",
        password: "",
        password2: "",
        valid: false,
        permanentSession: false,
      };
    },
    computed: {
      validateUsername() {
        const rules = [];

        if (!this.username) {
          const rule = this.$t("login.username_empty");
          rules.push(rule);
        }
        return rules;
      },
      validatePassword() {
        const rules = [];
        if (!this.password) {
          const rule = this.$t("login.password_empty");
          rules.push(rule);
        }
        return rules;
      },
    },
    mounted() {
      this.discoverServers();
    },
    methods: {
      discoverServers() {
        // discover Music Assistant servers on the network
        // if we're running in the browser this is pretty limited as we can't do mdns/avahi discovery.
        // TODO: Implement avahi discovery for native clients.

        // Support for frontend included in the server build
        let serverAddress = window.location.href;
        serverAddress = serverAddress.split("#")[0]; // strip off router path
        serverAddress = serverAddress.replace("8080", "8095"); // dev
        if (!serverAddress.endsWith("/")) {
          serverAddress = serverAddress + "/";
        }
        axios
          .get(serverAddress + "info")
          .then((response) => {
            // server running at this endpoint
            this.servers = [response.data];
            // select discovered server
            if (!this.selectedServer) {
              this.selectedServer = this.servers[0];
              // always use the real address because we might be running behinf reverse proxy
              this.selectedServerAddress = serverAddress;
            }
          })
          .catch((e) => {
            Vue.$log.error("Server discovery failed", e);
            this.$store.dispatch("dispatchNotification", {
              content: "Server discovery failed",
              type: "error",
            });
            this.servers = [];
            // TODO: Use remote connect feature here
          });
      },

      async submitLogin() {
        // Login to server with username and password
        this.$api
          .login(
            this.selectedServerAddress,
            this.username,
            this.password,
            this.permanentSession ? "browser" : undefined
          )
          .then((response) => {
            // login success
            this.$store.dispatch("dispatchNotification", {
              content: "Connected to Music Assistant server",
              type: "success",
            });
            // store token in localstorage
            localStorage.setItem("serverAddress", this.selectedServerAddress);
            localStorage.setItem("serverToken", response.data.token);
            this.$router.push({ name: "home" });
          })
          .catch((err) => {
            this.$store.dispatch("dispatchNotification", {
              content: "Error while connecting to server: " + err,
              type: "error",
            });
          });
      },
      async submitSetup() {
        // Setup server with username and password
        this.$api
          .setup(this.selectedServerAddress, this.username, this.password)
          .then(() => {
            // setup success
            this.submitLogin();
          })
          .catch((err) => {
            this.$store.dispatch("dispatchNotification", {
              content: "Error while connecting to server: " + err,
              type: "error",
            });
          });
      },
      resetValidation() {
        this.$refs.form.resetValidation();
      },
    },
  });
</script>
